import { log } from "util";
import screenfull from "screenfull";
export default {
  name: "AdminUser",
  data() {
    return {
      // 管理员信息数据
      tableData: [
        {
          date: "2016-05-02",
          name: "王小虎1",
          sex: "男",
          phone: "18524586235",
          // 状态按钮
          value: true,
          imgSrc: "img/logo.72a44f24.png"
        },
        {
          date: "2016-05-04",
          name: "王小虎2",
          sex: "男",
          phone: "18524586235",
          // 状态按钮
          value: true,
          imgSrc: "img/logo.72a44f24.png"
        },
        {
          date: "2016-05-01",
          name: "王小虎3",
          sex: "男",
          phone: "18524586235",
          // 状态按钮
          value: false,
          imgSrc: "img/logo.72a44f24.png"
        },
        {
          date: "2016-05-03",
          name: "王小虎4",
          sex: "男",
          phone: "18524586235",
          // 状态按钮
          value: true,
          imgSrc: "img/logo.72a44f24.png"
        }
      ],
      // 信息编辑
      outerVisible: false,
      innerVisible: false,
      // 搜索信息
      value9: "",
      input10: "",
      // 添加信息
      dialogFormVisible: false,
      form: {
        name: ""
      },
      formLabelWidth: "60px",
      // 性别下拉框
      sexOptions: [
        {
          value: "1",
          label: "男"
        },
        {
          value: "0",
          label: "女"
        }
      ],
      sexValue: "男",
      // 状态下拉框
      stateOptions: [
        {
          value: "1",
          label: "启用"
        },
        {
          value: "0",
          label: "禁用"
        },
        {
          value: "2",
          label: "全部"
        }
      ],
      stateValue: "全部",
      // 头像上传
      dialogImageUrl: "",
      dialogVisible: false
    };
  },
  methods: {
    formatter(row, column) {
      return row.address;
    },
    // 编辑管理员信息
    handleEdit(index, row) {
      console.log(index, row);
      // 编辑信息
      this.outerVisible = true;
    },
    // 删除管理员信息
    handleDelete(index, row) {
      console.log(row);

      this.$confirm(`确定删除 ${row.name} 吗?`, "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    isUpdate() {
      this.$confirm("确定修改吗?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "修改成功!"
          });
          this.outerVisible = false;
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消修改"
          });
          this.outerVisible = false;
        });
    },
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    // 添加信息
    addAdminUser() {
      this.dialogFormVisible = true;
    },
    // 确定添加
    isAddUser() {
      this.dialogFormVisible = false;
      this.$message({
        message: "添加成功",
        type: "success"
      });
    },
    // 头像上传
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    }
  }
};
